}

li.favorite-wrapper-item {
  background-color: var(--bcg-color-container-mobile);
}

form.form-toogle {
  display: flex;
}

.checkboxBtn, .checkboxBtnOrange {
  display: none;
}

.checkboxBtn + .lb1, .checkboxBtn + .lb1:before, .checkboxBtn + .lb1:after, .checkboxBtnOrange + .lb1, .checkboxBtnOrange + .lb1:before, .checkboxBtnOrange + .lb1:after {
  transition: all .3s;
}

.checkboxBtn + .lb1:before, .checkboxBtnOrange + .lb1:before {
  content: attr(data-offlabel);
  color: #000;
  font-family: Open Sans, sans-serif;
  font-size: 19px;
  display: block;
  position: absolute;
  top: 18px;
  right: 10px;
}

.checkboxBtn:checked + .lb1:before, .checkboxBtnOrange:checked + .lb1:before {
  content: attr(data-onlabel);
  color: #fff;
  left: 20px;
  right: auto;
}

.checkboxBtnOrange:checked + .lb1:before {
  right: 20px;
}

.checkboxBtn:checked + .lb1:after {
  background-color: #fff;
  left: 24px;
}

.checkboxBtnOrange:checked + .lb1:after {
  background-color: #fff;
  left: 1px;
}

.checkboxBtn + .lb1 {
  background-color: #fff;
}

.checkboxBtn:checked + .lb1, .checkboxBtnOrange:checked + .lb1 {
  background-color: #fd5103;
}

.checkboxBtn:checked + .lb1:before {
  color: #fff;
}

.checkboxBtn + .lb1, .checkboxBtnOrange + .lb1 {
  width: 34px;
  height: 14px;
  cursor: pointer;
  border: 1px solid #fd5103;
  border-radius: 30px;
  display: inline-block;
  position: relative;
}

.checkboxBtn + .lb1:after {
  content: "";
  width: 11px;
  height: 11px;
  background-color: #fd5103;
  border-radius: 50%;
  position: absolute;
  top: .5px;
  left: 1px;
}

.checkboxBtnOrange + .lb1:after {
  content: "";
  width: 11px;
  height: 11px;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  top: .5px;
  left: 1px;
}

.lb1 {
  color: var(--text-color);
  margin-left: 8px;
  font-size: 14px;
}

.checkboxBtnOrange + .lb1 {
  width: 34px;
  height: 14px;
  background-color: #fd5103;
}

.checkboxBtnOrange + .lb1:after {
  content: "";
  width: 11px;
  height: 11px;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  left: 1px;
}

body.mouse .site-nav-favorite-wrapper:hover .favorite-wrapper {
  opacity: 1;
  background-color: #fff;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

body.mouse .site-nav-favorite-wrapper:hover .arrow {
  border-top: 8px solid #fd5103;
  border-left: 8px solid rgba(0, 0, 0, 0);
  border-right: 8px solid rgba(0, 0, 0, 0);
  transition: border-top .25s cubic-bezier(.4, 0, .2, 1);
}

.site-nav-favorite-wrapper .open {
  opacity: 1;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
}

div.icons-all {
  display: none;
}

.icons-all svg {
  width: 17px;
  height: 15px;
}

