  }

  .menu__icon span, .menu__icon:before, .menu__icon:after {
    width: 100%;
    height: 10%;
    background-color: var(--color-btn-burger);
    transition: all .3s;
    position: absolute;
    left: 0;
  }

  .menu__icon:before, .menu__icon:after {
